  border:1px solid black !important;
  margin:3px !important;
}
content{
  padding-top: 20px;
  padding-bottom: 20px;
  position: relative;
  display: table;
  margin: auto;
}

.MuiButton-label{
  display:inline !important;
}

h1,h2,h3,h4,h5{
